
// Breakpoints
$breakpoints: (
  'phone': 420px,
  'tablet': 768px,
  'laptop': 1024px,
  'desktop': 1400px
);

:root {
    // Colors
    --red-base: #ed2225;
    --red-dark: rgba(237, 34, 37, 0.75);
    --yellow-base: #fff100;
    --yellow-dark: #fff100;
    --purple-base: #92278f;
    --purple-light: hsl(302, 40%, 60%);
    --purple-dark: rgba(146, 39, 143, 0.75);
    --light: rgb(241, 241, 241);
    --gray: rgb(71, 71, 71);
    --gray-dark: rgb(44, 44, 44);
    --dark: #000;
    --second-dark: #111111;

    // Fonts
    --title-font: 'Roboto Condensed', sans-serif;
    --body-font: 'Montserrat', sans-serif;
    
    --title-size: 2.6rem;
    --menu-top-size: 2.2rem;
    --menu-bottom-size: var(--menu-top-size);
    --shows-size: 1.95rem;
    --font-size: 1.7rem;
    --min-size: 1.5rem;

    --show-size: 10rem;
    
    --line-height: 1.6;
    --fw-light: 300;
    --fw-normal: 400;
    --fw-bold: 700;

    @include media('>=phone') {
        // --show-size: 17rem;
    }

    @include media('>=tablet') {
        --title-size: 3rem;
        --menu-top-size: 1.8rem;
        --menu-bottom-size: 1.6rem;

        --show-size: 21rem;
    }
    @include media('>=laptop') {
        --title-size: 3.1rem;
        --menu-top-size: 3rem;
        --menu-bottom-size: 2.4rem;
        --min-size: 1.6rem;
    }


    --max-width: 120rem;
    // --header-height: 5rem;


    --logo-width: 14rem;
    --logo-height: 4rem;
    --logo-spacing: .5rem;

    --height-panel: 0;

    @include media('>=tablet') {
        --logo-width: 15rem;
    }
    @include media('>=laptop') {
        --logo-width: 24rem;
    }

    --h1: 3.2rem;
    --h2: var(--title-size);
    --h3: 2.0rem;

    --bezier-curve: cubic-bezier(0.65, 0.05, 0.35, 1);
    // --bezier-curve: cubic-bezier(0.4, 0, 0.2, 1.01)
    --header-height: 92.9219px;
}

// Pasando a variables de Sass
$red-base: #ed2225;
$red-dark: rgba(237, 34, 37, 0.75);
$yellow-base: #fff100;
$yellow-dark: #fff100;
$purple-base: #92278f;
$purple-light: hsl(302, 40%, 60%);
$purple-dark: rgba(146, 39, 143, 0.75);
$light: rgb(241, 241, 241);
$gray: rgb(71, 71, 71);
$gray-dark: rgb(44, 44, 44);
$dark: #000;
$second-dark: #111111;

$title-font: var(--title-font);
$body-font: var(--body-font);

$title-size: var(--title-size);
$menu-top-size: var(--menu-top-size);
$menu-bottom-size: var(--menu-bottom-size);
$shows-size: var(--shows-size);
$font-size: var(--font-size);
$min-size: var(--min-size);

$show-size: var(--show-size);

$fw-light: var(--fw-light);
$fw-normal: var(--fw-normal);
$fw-bold: var(--fw-bold);
$line-height: var(--line-height);


$second-dark: var(--second-dark);
$max-width: var(--max-width);
$header-height: var(--header-height);
$logo-width: var(--logo-width);
$logo-height: var(--logo-height);
$logo-spacing: var(--logo-spacing);

$h1: var(--h1);
$h2: var(--h2);
$h3: var(--h3);

$sp-1: 1rem;
$sp-2: 2rem;
$sp-3: 3rem;
$sp-4: 4rem;
$sp-5: 5rem;

$spaceamounts: (1,2,3,4,5);


// z-index
$z-index: (
    tooltip: 100,
    modal: 500,
    overlay: 750,
    offcanvas: 1000
);